import uniDrawer from '@/components/uni-drawer/uni-drawer.vue';
import uniPopup from '@/components/uni-popup/uni-popup.vue';
import FwList from '@/pages/fwList/fwList.vue';
import { mapState, mapMutations } from 'vuex';
export default {
	computed: {
		...mapState(['img_url','api'])
	},
	components: { uniDrawer, FwList, uniPopup },
	data() {
		return {
			fubz: [
				{
					url: '3_jingyan.png',
					title: '经验丰富',
					center: '多年经验为您保驾护航'
				},
				{
					url: '3_fuwu.png',
					title: '一站式服务',
					center: '各个环节保障服务顺畅'
				},
				{
					url: '3_gaoxiao.png',
					title: '一对一高效服务',
					center: '专业对接人员,帮你节省不必要环节'
				},
				{
					url: '3_zhuanye.png',
					title: '专业品质',
					center: '服务均由3年以上资深人士操作保障品质'
				},
				{
					url: '3_baomi.png',
					title: '信息保密',
					center: '自有服务器保障客户数据安全专业人士保障客户资料免遭外泄'
				},
				{
					url: '3_quancheng.png',
					title: '全程保密',
					center: '售前咨询,下单5分钟内响应关键环节100%提醒'
				}
			],
			times_: '',
			company: '',
			full_name: '',
			mobile: '',
			msg: '',
			tanchu_center: '',
			tanchu_red: ''
		};
	},
	onLoad() {},
	methods: {
		tijiao_() {
			var that = this;
			uni.request({
				url: that.api + '/add_hm',
				method: 'POST',
				data: {
					company: that.company,
					name: that.full_name,
					phone: that.mobile,
					leav: that.msg
				},
				success: res => {
					if (res.data == 1) {
						that.tanchu_center = '恭喜您提交成功！';
						that.tanchu_red = '（我们将尽快与您取得联系，请您务必保持的手机畅通！）';
						that.$refs.popup.open();
					}else{
						that.tanchu_center = '提交失败！';
						that.tanchu_red = '';
						that.$refs.popup.open();
					}
				}
			});
		}
	}
};
